.home-page {
  @include background("../images/background-rocks.png");
  @media (max-height: 500px) {
    background: none;
  }
}

.home-page-top {
  // @include animation-slideup(slideup, 2.5s, 1);
  @include primary-container(50px);
  height: 90vh;
  -webkit-align-items: center;
  align-items: center;
  display: -webkit-flex;
  display: flex;
  .row {
    max-width: 800px;
    h1 {
      @include primary-text(2.0rem, left, $secondary-color, $secondary-color);
      margin-top: 25px;
      @media (max-width: 425px) {
        font-size: 1.5rem;
      }
    }
    h2 {
      @include primary-text(2.25rem, left, $primary-color, $primary-color);
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      line-height: 125%;
      margin-top: 25px;
      @media (max-width: 425px) {
        font-size: 1.75rem;
      }
    }
    p {
      @include primary-text(1.25rem, left, $grey, $grey);
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      margin-top: 15px;
      @media (max-width: 425px) {
        font-size: 1.0rem;
      }
    }
  }
}

.home-page-bottom {
  height: 10vh;
}
