#loader-wrapper {
  background: $off-white;
  height: 100%;
  position: fixed; top: 0; left: 0;
  width: 100%;
  z-index: 1000;
  #loader {
    display: block;
    position: relative; left: 50%; top: 50%;
    width: 75px;
    height: 75px;
    margin: -37.5px 0 0 -37.5px;
    .square {
      .dot {
        animation: loader-disappear .8s alternate infinite;
        background: $primary-color;
        border-radius: 50px;
        display: inline-block;
        height: 10px;
        margin: 5px;
        width: 10px;
        &:nth-child(1) {
          animation-delay: .2s;
        }
        &:nth-child(2) {
          animation-delay: .4s;
        }
        &:nth-child(3) {
          animation-delay: .6s;
        }
        &:nth-child(4) {
          animation-delay: .4s;
        }
        &:nth-child(5) {
          animation-delay: .6s;
        }
        &:nth-child(6) {
          animation-delay: .8s;
        }
        &:nth-child(7) {
          animation-delay: .6s;
        }
        &:nth-child(8) {
          animation-delay: .8s;
        }
        &:nth-child(9) {
          animation-delay: 1s;
        }
      }
    }
  }
}
