@font-face {
	font-family: 'league spartan bold';
	src: url('../fonts/league-spartan/league-spartan-bold.eot'); /* ie9 */
	src: url('../fonts/league-spartan/league-spartan-bold.eot?#iefix') format('embedded-opentype'), /* ie6-ie8 */
		url('../fonts/league-spartan/league-spartan-bold.woff2') format('woff2'), /* modern browsers */
		url('../fonts/league-spartan/league-spartan-bold.woff') format('woff'), /* modern browsers */
		url('../fonts/league-spartan/league-spartan-bold.ttf') format('truetype'), /* safari, android, ios */
		url('../fonts/league-spartan/league-spartan-bold.svg#LeagueSpartan-Bold') format('svg'); /* legacy ios */
}

@font-face {
	font-family: 'libre baskerville';
	src: url('../fonts/libre-baskerville/libre-baskerville.eot'); /* ie9 */
	src: url('../fonts/libre-baskerville/libre-baskerville.eot?#iefix') format('embedded-opentype'), /* ie6-ie8 */
		url('../fonts/libre-baskerville/libre-baskerville.woff2') format('woff2'), /* modern browsers */
		url('../fonts/libre-baskerville/libre-baskerville.woff') format('woff'), /* modern browsers */
		url('../fonts/libre-baskervillek/libre-baskerville.ttf') format('truetype'), /* safari, android, ios */
		url('../fonts/libre-baskervillek/libre-baskerville.svg#LibreBaskerville-Regular') format('svg'); /* legacy ios */
}
