.modal-content {
  background: url("../images/map.png");
  background-position: center;
  border: none;
  border-radius: 5px;
  p {
    @include primary-text(1.0rem, left, $primary-color, $primary-color);
    font-family: 'league spartan bold', 'helvetica neue', helvetica, sans-serif;
    margin-bottom: 50px;
  }
}

.modal-header {
  border-bottom: 0px;
  padding-bottom: 0px;
}

.modal-body {
  .contact-form {
    background-color: transparent;
    border-radius: .25rem;
    padding: 25px;
    width: 100%;
  }
}
