// keyframe rule specifying the animation selector (not in use)
@-webkit-keyframes slideup {
  0%   {top: 100px; opacity: 0; }
  15%  {top: 100px; opacity: 0; }
  50%  {top: 0px; }
  75%  {top: 0px; }
  100% {top: 0px; }
}

@keyframes slideup {
  0%   {top: 100px; opacity: 0; }
  15%  {top: 100px; opacity: 0; }
  50%  {top: 0px; }
  75%  {top: 0px; }
  100% {top: 0px; }
}

// preloader 
@keyframes loader-disappear {
  0% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(0, 0);
  }
}
