.about-page {
  @include background("../images/background-surf.png");
  @media (max-height: 500px) {
    background: none;
  }
}

.about-page-top {
  // @include animation-slideup(slideup, 2.5s, 1);
  @include primary-container(50px);
  height: 90vh;
  -webkit-align-items: center;
  align-items: center;
  display: -webkit-flex;
  display: flex;
  .row {
    max-width: 800px;
    h2 {
      @include primary-text(2.25rem, left, $primary-color, $primary-color);
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      line-height: 125%;
      margin: 0 0 0 0;
      @media (max-width: 425px) {
        font-size: 1.75rem;
      }
    }
    p {
      @include primary-text(1.25rem, left, $grey, $grey);
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      margin-top: 15px;
      @media (max-width: 425px) {
        font-size: 1.0rem;
      }
    }
    img {
      max-width: 50px;
      width: 100%;
    }
  }
}

.about-page-bottom {
  height: 10vh;
}
