.journal-page {
  @include background("../images/background-sand.png");
  @media (max-height: 500px) {
    background: none;
  }
}

.journal-page-top {
  // @include animation-slideup(slideup, 2.5s, 1);
  @include primary-container(50px);
  height: 90vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  @media (max-width: 768px) {
    height: auto;
    min-height: 500px;
  }
  .row {
    max-width: 1000px;
    h2 {
      @include primary-text(2.25rem, left, $primary-color, $primary-color);
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      line-height: 125%;
      margin: 0 0 0 0;
      @media (max-width: 425px) {
        font-size: 1.75rem;
      }
    }
    p {
      @include primary-text(1.25rem, left, $grey, $grey);
      display: inline-block;
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      margin: 25px 0 0 0;
      @media (max-width: 425px) {
        font-size: 1.0rem;
      }
    }
    img {
      margin: 25px 0 0 0;
      max-width: 50px;
      width: 100%;
    }
  }
}

.journal-page-top {
  .card {
    @include box-shadow(0 20px 20px rgba(0,0,0,.08),0 20px 20px rgba(0,0,0,.08));
    @include transition(transform .5s);
    border: none;
    border-radius: 5px;
    margin: 25px 0 0 0;
    &:hover {
      transform: translateY(-15px);
    }
    .card-image {
      img {
        height: auto;
        margin: 0;
        max-width: none;
      }
    }
    .card-body {
      padding: 15px;
      p.title {
        @include primary-text(1.25rem, left, $primary-color, $primary-color);
        font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
        margin: 0 0 5px 0;
      }
      p {
        @include primary-text(0.90rem, left, $grey, $grey);
        font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
        margin: 0;
      }
    }
  }
}

.journal-page-bottom {
  height: 10vh;
}
