/************* clean up ninja form ****************/

// removes red asterisk
.ninja-forms-req-symbol {
    display: none;
}

// removes "fields marked with an * are required"
.nf-form-fields-required {
  display: none;
}

// remove blue focus
input:focus, textarea:focus, select:focus {
  outline: none;
}

// removes error message
.nf-error-msg, .ninja-forms-req-symbol {
    display: none;
}

/************* ninja form style ****************/

.contact-form {

.ninja-forms-form-wrap input, select, textarea {
  background-color: transparent;
  border: none;
  border-bottom: 2px solid $primary-color;
  color: $primary-color;
  font-size: 1rem;
  padding-bottom: 15px;
}

// set the height on the textarea only
.nf-field-element textarea {
    height: 150px;
}


// submission success
.nf-response-msg {
  color: $secondary-color;
  font-size: 1rem;
}

[id*="nf-field-"]  input[type=button] {
  @include button($secondary-color, $secondary-color, transparent);
  @include primary-text(1.0rem, center, $off-white, $secondary-color);
  @include box-shadow(10px 10px 45px 5px lighten($grey, 50%), 10px 10px 45px 5px lighten($grey, 45%));
  font-family: 'league spartan bold', 'helvetica neue', helvetica, sans-serif;
  width: 100%;
}

} // .contact-form
