.work-page {
  @include background("../images/background-lifeguard.png");
  @media (max-height: 500px) {
    background: none;
  }
}

.work-page-top {
  // @include animation-slideup(slideup, 2.5s, 1);
  @include primary-container(50px);
  height: 90vh;
  -webkit-align-items: center;
  align-items: center;
  display: -webkit-flex;
  display: flex;
  .row {
    max-width: 1000px;
    h2 {
      @include primary-text(2.25rem, left, $primary-color, $primary-color);
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      line-height: 125%;
      margin: 0;
      @media (max-width: 425px) {
        font-size: 1.75rem;
      }
    }
    p {
      @include primary-text(1.25rem, left, $grey, $grey);
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      margin: 15px 0 0 0;
      @media (max-width: 425px) {
        font-size: 1.0rem;
      }
    }
    img {
      margin: 25px 0 0 0;
      max-width: 50px;
      width: 100%;
    }
  }
}

.work-page-bottom {
  height: 10vh;
}
