body {
  font-family: 'league spartan bold', 'helvetica neue', helvetica, sans-serif;
  @include font-smoothing(on);
  background-color: $off-white;
}

html {
  font-size: 1rem;
}

hr {
  margin: 25px 0 25px 0;
  border: 0;
  border-top: 2px solid lighten($grey, 50%);
}

::-moz-selection {
  color: $off-white;
  background: $primary-color;
}

::selection {
  color: $off-white;
  background: $secondary-color;
}

// pseudo classes
a {
  color: $primary-color;
  &:hover, &:active, &:focus {
    color: $secondary-color;
  }
}

a.border {
  background: url("../images/border-image.png") repeat-x 100% 100%/100% 5px;
  color: $grey;
  &:hover, &:active, &:focus {
    color: $grey;
  }
}
