@mixin primary-text($font-size, $text-align, $color, $hover-color) {
  font-size: $font-size;
  font-weight: 400;
  text-align: $text-align;
  color: $color;
  &:visited, &:hover, &:focus, &:active {
    color: $hover-color;
  }
}

@mixin button($background-color, $border-color, $hover-background-color) {
  background-color: $background-color;
  border: 3px solid $border-color;
  padding: 10px;
  -webkit-transition: background-color .5s ease;
  -moz-transition: background-color .5s ease;
  -o-transition: background-color .5s ease;
  transition: background-color .5s ease;
  &:hover {
    background-color: $hover-background-color
  }
}

@mixin primary-container($padding) {
  padding: $padding;
  margin: auto;
  max-width: 1300px;
  @media (max-width: 425px) {
    padding: 25px;
  }
}

@mixin animation-slideup($animation-name, $animation-duration, $animation-iteration) {
  position: relative;
  -webkit-animation-name: $animation-name;
  -webkit-animation-duration: $animation-duration;
  -webkit-animation-iteration-count: $animation-iteration;
  animation-name: $animation-name;
  animation-duration: $animation-duration;
  animation-iteration-count: $animation-iteration;
}

@mixin font-smoothing($value: on) {
  @if $value == on {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  @else {
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: auto;
  }
}

@mixin background($background) {
  background: url($background);
  background-size: 100% auto, cover;
  background-position: center bottom;
  background-repeat: no-repeat;
}

@mixin box-shadow($color, $color-hover) {
  -webkit-box-shadow: $color;
  -moz-box-shadow: $color;
  box-shadow: $color;
  &:hover {
    -webkit-box-shadow: $color-hover;
    -moz-box-shadow: $color-hover;
    box-shadow: $color-hover;
  }
}

@mixin transition($effect) {
  -webkit-transition: $effect;
  -moz-transition: $effect;
  -o-transition: $effect;
  transition: $effect;
}
