.single-page {
  @include primary-container(50px);
  background: url("../images/background-pattern.png");
}

.single-page-about {
  margin: 100px auto 0 auto;
  max-width: 800px;
  p {
    @include primary-text(1.25rem, left, $grey, $grey);
    display: inline-block;
    line-height: 200%;
    font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
    margin: 0;
    @media (max-width: 425px) {
      font-size: 1.0rem;
    }
  }
}

.single-page-top {
  margin: 50px 0 0 0;
  .row {
    margin: 0 auto;
    max-width: 800px;
    h1 {
      @include primary-text(2.25rem, left, $primary-color, $primary-color);
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      line-height: 125%;
      margin-bottom: 0;
      @media (max-width: 425px) {
        font-size: 1.75rem;
      }
    }
    p {
      @include primary-text(1.25rem, left, $grey, $grey);
      display: inline-block;
      line-height: 200%;
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      margin: 25px 0 0 0;
      @media (max-width: 425px) {
        font-size: 1.0rem;
      }
    }
    // drop cap trick
    p:nth-of-type(2):first-letter {
      color: $secondary-color;
      float: left;
      font-size: 40px;
      font-weight: 600;
      line-height: 1;
      margin: 0 10px 0 0;
    }
    a {
      color: $secondary-color;
    }
    blockquote {
      @include primary-text(1.25rem, left, $grey, $grey);
      border-left: 5px solid $secondary-color;
      font-family: 'libre baskerville', 'helvetica neue', helvetica, sans-serif;
      font-style: italic;
      margin: 25px 0 0 0;
      padding: 15px 0 15px 20px;
      @media (max-width: 425px) {
        font-size: 1.0rem;
      }
      p {
        margin: 0;
      }
    }
    img {
      height: auto;
      margin-top: 25px;
      width: 100%;
    }
  }
}

.single-page-bottom {
  margin: 0 auto;
  max-width: 800px;
}
