.navbar {
  @include primary-container(25px);
  background-color: rgba(251, 251, 251, 0.75);
  .row {
    height: 50px;
    margin: 0px;
    float: right;
    .col {
      margin-left: 5px;
      margin-right: 5px;
    }
  }
}

.hamburger-icon {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  flex-direction: column;
  hr {
    background-color: $secondary-color;
    border-top: none;
    height: .25em;
    margin: 2.5px 0px 2.5px 0px;
    padding: 0;
    width: 35px;
  }
}

.collapse, .collapse.in, .collapsing {
  background-color: rgba(251, 251, 251, 0.75);
  margin: auto;
  max-width: 1300px;
  padding-left: 25px;
  padding-right: 25px;
  text-align: right;
  .collapse-bar {
    padding-top: 25px;
    a {
      @include primary-text(1.0rem, center, $secondary-color, $secondary-color);
      padding-left: 5px;
      padding-right: 5px;
      // remove bootrap underline
      &:hover {
        text-decoration: none;
      }
      @media (max-width: 425px) {
        font-size: 0.9rem;
      }
    }
  }
}
